.WelcomeScreen {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	background: white;
	display: flex;
	justify-content: center; /* Center horizontally */
	align-items: center; /* Center vertically */
	flex-direction: column; /* Stack elements vertically */
}

.login-button {
	color: #494949;
	text-transform: uppercase;
	text-decoration: none;
	background: #ffffff;
	border: 4px solid #494949;
	display: inline-block;
	transition: all 0.4s ease 0s;
	min-width: 10rem;
	margin: 1rem;
}

.button_cont {
	display: flex;
	justify-content: center; /* Center horizontally */
	margin-bottom: 2rem; /* Add bottom margin for spacing */
}

.button {
	width: 184px;
	height: 42px;
	border-radius: 2px;
	box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
	background: none; /* Add this line to remove the background */
}

.google-btn:hover {
	box-shadow: 0 0 6px #4285f4;
}

.google-btn:active {
	background: #1669f2;
}

.google-icon-wrapper {
	position: absolute;
	margin-top: 1px;
	margin-left: 1px;
	width: 40px;
	height: 40px;
	border-radius: 2px;
	background-color: #fff;
}

.google-icon {
	position: absolute;
	margin-top: 11px;
	margin-left: -8px;
	width: 18px;
	height: 18px;
}

.btn-text {
	color: #fff;
	font-size: 14px;
	letter-spacing: 0.2px;
	font-family: "Roboto";
	background-color: #4285f4;
	width: 100%;
	height: 100%;
	text-align: center;
}
